.articles-list {
    @include desktop {
        @include base-grid;
        grid-row-gap: 60px;
    }

    &__item {
        width: 100%;
        margin-bottom: 40px;

        @include desktop {
            margin-bottom: 0;
        }
    }

    & > div {
        display: flex;
        align-items: center;

        .article-card {
            width: 100%;
        }

        @include mobile-only {
            &:nth-child(odd) {
                .article-card__media:before {
                    padding-top: percentage(400/304);
                }
            }

            &:nth-child(even) {
                .article-card__media:before {
                    padding-top: percentage(280/304);
                }
            }
        }

        &:nth-child(4n+1) {
            @include desktop {
                grid-column: col / span 5;
                grid-column-start: 2;
            }
        }

        &:nth-child(4n+2) {
            @include desktop {
                grid-column: col / span 3;
                grid-column-start: 8;
            }
        }

        &:nth-child(4n+3) {
            @include desktop {
                grid-column: col / span 3;
                grid-column-start: 3;
            }
        }

        &:nth-child(4n+4) {
            @include desktop {
                grid-column: col / span 5;
                grid-column-start: 7;
            }
        }
    }

    &__btn {
        text-transform: uppercase;
    }

    &__btn-wrapper {
        display: flex;
        justify-content: center;
        padding-bottom: 70px;

        @include desktop {
            padding-top: 5px;
            padding-bottom: 170px;
        }
    }

    .article-card__content {
        @include desktop {
            min-height: 222px;
        }
    }
}

.articles-list-wrapper {
    padding: 75px 0 0 0;

    @include desktop {
        padding: 170px 0 0 0;
    }
}
